
// Import components
import React, {Component} from 'react';
import {getPages} from './GetData';


class PageList extends Component {

	// Definite state array as pages
	state = {
		pages: []
	}

	componentDidMount() {
	  	let postUrl = `${getPages}`;
	  	fetch(postUrl)
	    .then(results => results.json())
	    .then(results => {
	        this.setState({
	           pages: results
	        })
	    })
	}
	render() {
		let pagesArray = this.state.pages;
		let pageTitle;
		let pageContent;
		pagesArray.forEach(page => {
			let slugUrl = '/' + page.slug + '/';
			if (slugUrl === window.location.pathname) {
				pageTitle = page.title.rendered;
				pageContent = page.content.rendered;
			} else if (slugUrl === '/home/' && window.location.pathname === '/') {
				pageTitle = page.title.rendered;
				pageContent = page.content.rendered;
			}
		});
		return (
		 	<article>
		   		<p>{pageTitle}</p>
		   		<p dangerouslySetInnerHTML={{__html: pageContent}}/>
		 	</article>
		);
	}
}
export default PageList;


