import React, { Component } from 'react';
import './css/style.css';
import MainMenu from './navigations/MainMenu';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import HomeTemplate from './template/Home';
import ImpactTemplate from './template/Impact';
import WorkTemplate from './template/Work';
import TeamTemplate from './template/Team';
import ContactTemplate from './template/Contact';


function Home() {
    return <HomeTemplate/>
}
function Impact() {
    return <ImpactTemplate/>
}
function Work() {
    return <WorkTemplate/>
}
function Team() {
    return <TeamTemplate/>
}
function Contact() {
    return <ContactTemplate/>
}


class App extends Component {
    render() {
        return (
            <div className="App">
                <Router>
                
                    <MainMenu/>
                    <Switch>
                        <Route exact path="/" component={Home} />
                        <Route path="/impact/" component={Impact} />
                        <Route path="/our-work/" component={Work} />
                        <Route path="/our-team/" component={Team} />
                        <Route path="/contact/" component={Contact} />
                    </Switch>
                </Router>
            </div>
        );
    }
}
export default App

