
// Import components
import React, {Component} from 'react';
import {getAcfPages} from './GetData';


class HomeBanner extends Component {

	// Definite state array as acf
	state = {
		acf: []
	}

	componentDidMount() {
	  	let acfUrl = `${getAcfPages}`;
	  	fetch(acfUrl)
	    .then(results => results.json())
	    .then(results => {
	        this.setState({
	           acf: results
	        })
	        //console.log(results)
	    })
	}
	render() {
		let acfArray = this.state.acf;
		//let acfBanner;
		let bannerResults;
		console.log(acfArray);
		acfArray.forEach(array => {
			//console.log(array);
			if (array.acf.length !== 0) {
				//array.acf.banner.forEach(banner => {
					//console.log(array.acf.banner);
					bannerResults = array.acf.banner.map((banner, index) => {
						console.log(banner.title);
						console.log(banner.bg_image.url);
						return (
							<div key={index}>
								<h4>{banner.title}</h4>
								<img src={banner.bg_image.url} alt={banner.bg_image.alt} />
						    </div>
						)
					})
			}
		})
   		
		return (
		 	<div>
		   		<h1>ACF Results</h1>
		   		{bannerResults}
		 	</div>
		);
	}
}
export default HomeBanner;

