import React from 'react';
import { Link } from 'react-router-dom';
import logo from './../images/logo.svg';

function HeaderLogo() {
	return (
		<div className="site-logo">
			<Link to="/" title="Home">
				<img src={logo} className="logo" alt="Innermost Digital Logo" />
			</Link>
		</div>
	);
}
export default HeaderLogo;


